.header {
    overflow: hidden;
    background-color: #333;
}

.header a {
    float: left;
    display: block;
    color: #f2f2f2;
    text-align: center;
    padding: 14px 16px;
    text-decoration: none;
    font-size: 17px;
}

.header a:hover:not(.icon) {
    background-color: #111;
}

.header .icon {
    display: none;
}

@media screen and (max-width: 600px) {
    .header a:not(.active) {display: none;}
    .header button.icon {
        float: right;
        display: block;
        background-color: #333333;
    }
}

@media screen and (max-width: 600px) {
    .header.responsive {position: relative;}
    .header.responsive .icon {
        position: absolute;
        right: 0;
        top: 0;
    }
    .header.responsive a {
        float: none;
        display: block;
        text-align: left;
    }
}
button {
    background-color: cornflowerblue;
    color: rgb(250, 250, 250);
    border: none;
    margin: 2px;
    padding: 3px 10px;
    text-align: center;
    text-decoration: none;
}

.main {
    width: 100%;
/*
    margin: 0 0 0 15%;
*/
}

.main > ol {
    padding: 5px;
}

.main > ol > li {
    color: rgb(0, 134, 139);
    margin-bottom: 4px;
    padding: 5px;
    list-style-position: inside;
    border: 1px solid rgb(211,211,211);
    border-radius: 5px;
    box-shadow:         0 2px rgb(240,240,240);
    position: relative;
}

li > input {
    display: block;
    width: 100%;
    border-top: none;
    border-right: none;
    border-bottom: 1px dashed rgb(88,88,88);
    border-left: none;
}

summary {
    padding: 5px;
}

summary:hover {
    cursor: pointer;
}

details > p {
    color: green;
}

@media only screen and (max-width: 768px) {
    .main {
        width: 100%;
/*
        margin: 0;
*/
    }
    .main > ol {
        margin-top: 0;
    }
}
#wordsNavList {
    list-style-type: none;
    text-align: center;
}

#wordsNavList > li > a {
    display: block;
    text-decoration: none;
    color: #000;
}

#wordsNavList > li > span.score {
    position: absolute;
    bottom: 4px;
    right: 5px;
}
body {
    margin: 0;
    padding: 0;
    font-family: sans-serif;
}

